import React from "react";
import Select from 'react-select'
import Fetch  from "./Fetch.jsx"
import ListLink  from "./ListLink.jsx"

class Species extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      species: [], //Toutes les espces de l'API
      specie: [], //les especes slectionnes dans le select
      options: [], //les options du select
      films: [], //va rcuprer tous les films faits par Studio Ghibli
    };
  }

  async componentDidMount(){ //Quand la classe est mount on effectue cela
    let species = await Fetch.getSpecies() //Appelle de la fonction pour rcuprer les espces
    let films = await Fetch.getFilms() // Appelle de la fonction pour rcuprer les films
    this.setState({species: species})
    let options = [] //Option du select
    this.state.species.forEach(element => {
      options.push({ value: element, label: element })
    });
    this.setState({options: options, films: films})
  }

  handleOnChange(specie){ //Pour rcuprer l'espce choisie
    this.setState({specie: specie})
  }

  render() { //On affiche le Select avec toutes les espces
    if (this.state.species) { //Vrification des espces
      return (
        <div>
          <div id="data">
            <h3>Species List</h3>
            <div id="selector">
              <Select defaultValue={[]} isMulti options={this.state.options} onChange={this.handleOnChange.bind(this)}/>
            </div>
          </div>
          <ListLink specie={this.state.specie} films={this.state.films}/>
        </div>
      );
    }
    else {
      return <div>Loading...</div>
    }
  }
}

export default Species